import React from "react";

import "./WorkSessionItem.scss";
import { getWorkSessions, setWorkSessions, WorkSession } from "../api/estimateApi";
import { useCurrentWork } from "../hooks/useCurrentWork";
import { useElapsedMinutes } from "../hooks/useElapsed";
import { If } from "../jk/types/jsxControlStatements";
import { formatMinutes } from "../utils/timeParsing";
import { useProvidedTrello } from "../hooks/useProvidedTrello";
import { areDatesEqual } from "../utils/dateMath";
import { formatDate } from "../utils/dateFormatting";

type Props = {
    className?: string;
    session: WorkSession;
    onRefresh?: () => void;
}

export function WorkSessionItem(props: Props) {
    const t = useProvidedTrello();
    const session = props.session;

    const currentWork = useCurrentWork();
    const currentWorkMinutes = useElapsedMinutes(
        session.endDate === undefined
            ? currentWork.value?.startDate
            : undefined,
    );

    const totalWorkMinutes = session.workMinutes + currentWorkMinutes;

    return (
        <div className={`work-session ${props.className}`}>
            Start: {formatDate(session.startDate)}
            {" | "}
            Work: {formatMinutes(totalWorkMinutes)}
            <If condition={session.endDate !== undefined}>
                {" | "}
                &#9745;
            </If>
            <button
                className="mod-danger"
                onClick={e => t.popup({
                    type: "confirm",
                    title: "Delete session?",
                    message: "Are you sure you want to delete this session?",
                    confirmText: "Yes",
                    onConfirm: async tp => {
                        const sessions = await getWorkSessions(t);
                        const index = sessions.findIndex(s => areDatesEqual(s.startDate, session.startDate));

                        if (index >= 0) {
                            sessions.splice(index, 1);
                            await setWorkSessions(t, sessions);
                        }

                        props.onRefresh?.();

                        await tp.closePopup();
                    },
                    cancelText: "No",
                    onCancel: tp => tp.closePopup(),
                    mouseEvent: e.nativeEvent,
                })}
            >
                X
            </button>
        </div>
    );
}
