import React, { useState } from "react";

import "./WorkSessions.scss";
import { useProvidedTrello } from "../hooks/useProvidedTrello";
import { useWorkSessions } from "../hooks/useWorkSessions";
import { Choose, If, Otherwise, When } from "../jk/types/jsxControlStatements";
import { WorkSessionItem } from "./WorkSessionItem";

type Props = {
    className?: string;
}

export function WorkSessions(props: Props) {
    const t = useProvidedTrello();
    const workSessions = useWorkSessions(t);
    const [showAll, setShowAll] = useState(false);

    const length = workSessions.data?.length ?? 0;

    return (
        <div className={`work-sessions ${props.className}`}>
            <If condition={length > 0}>
                <div>
                    Work sessions:
                    {"   "}
                    <If condition={length > 1}>
                        <span
                            className='link-like'
                            onClick={() => setShowAll(current => !current)}
                        >
                            {!showAll ? "show all" : "hide older"}
                        </span>
                    </If>
                </div>
            </If>
            <div className='work-sessions-list'>
                <Choose>
                    <When condition={workSessions.data === undefined}>
                        <p>Loading...</p>
                    </When>
                    <Otherwise>
                        <Choose>
                            <When condition={showAll}>
                                {workSessions.data!.map(session => (
                                    <WorkSessionItem
                                        key={session.startDate.toISOString()}
                                        session={session}
                                        onRefresh={workSessions.refetch}
                                    />
                                ))}
                            </When>
                            <Otherwise>
                                <If condition={length > 0}>
                                    <WorkSessionItem
                                        session={workSessions.data![workSessions.data!.length - 1]}
                                        onRefresh={workSessions.refetch}
                                    />
                                </If>
                            </Otherwise>
                        </Choose>
                    </Otherwise>
                </Choose>
            </div>
        </div>
    );
}
