import React, { useState } from "react";
import { editCurrentWorkSession } from "../api/estimateApi";
import { useProvidedTrello } from "../hooks/useProvidedTrello";
import { useWorkSessions } from "../hooks/useWorkSessions";

type Props = {
    className?: string;
}

export function EndWorkSessionButton(props: Props) {
    const t = useProvidedTrello();
    const sessions = useWorkSessions(t);
    const [isLoading, setIsLoading] = useState(false);

    if (sessions.data === undefined
        || sessions.data.length === 0
        || sessions.data[sessions.data.length - 1].endDate !== undefined
    )
        return null;

    return (
        <button
            className={`end-work-session mod-danger ${props.className}`}
            onClick={async () => {
                if (isLoading)
                    return;

                setIsLoading(true);
                await editCurrentWorkSession(t, async current => ({
                    ...current,
                    endDate: new Date(),
                }));
                await sessions.refetch();
                setIsLoading(false);
            }}
        >
            {!isLoading ? "End session" : "..."}
        </button>
    );
}
