import { useCallback, useEffect, useMemo, useState } from "react";
import { useQuery, useQueryClient } from "react-query";

import { Trello } from "../types/trello";
import { noDeps } from "../jk/noDeps";
import { useProvidedTrello } from "./useProvidedTrello";
import { useTrelloRender } from "./useTrelloRender";

export function useCardDueCompletedQuery(t: Trello.PowerUp.IFrame) {
    const queryKey = "card";
    const queryClient = useQueryClient();

    const query = useQuery(
        queryKey,
        async () => {
            const card = await t.card("dueComplete");
            return card.dueComplete;
        },
        { staleTime: 1000 },
    );

    const setData = useCallback(
        (data: boolean) => queryClient.setQueryData(queryKey, data),
        [queryClient],
    );

    return useMemo(
        () => ({
            ...query,
            setData,
        }),
        [query, setData],
    );
}

export function useCardWithFunctions(t: Trello.PowerUp.IFrame, ...fields: (keyof Trello.PowerUp.Card)[]) {
    const [card, setCard] = useState<Trello.PowerUp.Card>();

    const refresh = useCallback(
        async () => {
            let fetched: Trello.PowerUp.Card;

            try {
                fetched = await t.card(...fields);
            }
            catch (error) {
                console.warn("no card in context");
                return;
            }

            // console.log("refreshed card complete", fetched.dueComplete);

            setCard(fetched);
        },
        noDeps,
    );

    useTrelloRender(t, refresh);

    return useMemo(
        () => ({
            card,
            refresh,
            setCard,
        }),
        [card],
    );
}

export function useCard(t: Trello.PowerUp.IFrame, ...fields: (keyof Trello.PowerUp.Card)[]) {
    const { card } = useCardWithFunctions(t, ...fields);
    return card;
}

export function useProvidedCard(...fields: (keyof Trello.PowerUp.Card)[]) {
    const t = useProvidedTrello();
    return useCard(t, ...fields);
}

export function useCardById(t: Trello.PowerUp.IFrame, cardId: string | undefined, ...fields: (keyof Trello.PowerUp.Card)[]) {
    const [card, setCard] = useState<Trello.PowerUp.Card>();

    useEffect(
        () => {
            async function refresh() {
                if (cardId === undefined)
                    return;

                const cards = await t.cards(...fields);
                for (const c of cards) {
                    if (c.id === cardId) {
                        setCard(c);
                        break;
                    }
                }
            }
            refresh();
            t.render(refresh);
        },
        [cardId],
    );

    return card;
}
