import React, { useState } from "react";

import "./BoardSummaryPage.scss";
import { getBoardEstimates, SummaryEstimate } from "../api/estimateApi";
import { exportEstimatesForMail, exportEstimatesToTsv, exportToFile } from "../utils/export";
import { useProvidedTrello } from "../hooks/useProvidedTrello";
import { useWorkSessions } from "../hooks/useWorkSessions";
import { WorkButton } from "../components/WorkButton";
import { WorkSessions } from "../components/WorkSessions";
import { EndWorkSessionButton } from "../components/EndWorkSessionButton";
import { DeployInfo } from "../components/DeployInfo";
import { useTrelloRender } from "../hooks/useTrelloRender";
import { If } from "../jk/types/jsxControlStatements";
import { useMarginMultiplier } from "../hooks/useMarginMultiplier";
import { MarginInput } from "../components/MarginInput";
import { formatDate } from "../utils/dateFormatting";
import { useLastInputTime } from "../hooks/useLastInputTime";
import { SummaryDetail } from "../components/SummaryDetail";

export default function BoardSummaryPage() {
    const t = useProvidedTrello();
    const [summary, setSummary] = useState<SummaryEstimate>();
    const margin = useMarginMultiplier(t);
    const workSessions = useWorkSessions(t);
    const lastInputTime = useLastInputTime();

    useTrelloRender(t, async () => {
        const fetched = await getBoardEstimates(t);
        setSummary(fetched);
    });

    if (summary === undefined)
        return (<p>Loading... </p>);

    return (
        <div className='summary'>
            <SummaryDetail
                className="section"
                summary={summary}
                margin={margin.value}
            />

            <MarginInput className="section" />

            <div className="section work">
                <WorkSessions />
                <div className="buttons">
                    <EndWorkSessionButton />
                    <WorkButton onEstimateChanged={() => workSessions.refetch()} />
                </div>
            </div>
            <If condition={lastInputTime}>
                <div>
                    Last activity: {formatDate(lastInputTime!, "yyyy-MM-dd hh:mm:ss")}
                </div>
            </If>
            <div className='export-section'>
                <div className='export-inputs'>
                    <button
                        className='mod-primary'
                        onClick={async () => exportToFile(
                            await exportEstimatesToTsv(t, margin.value),
                            "estimate.tsv",
                        )}
                    >
                        Export estimates
                    </button>
                    <button
                        className='mod-primary'
                        onClick={async () => exportToFile(
                            await exportEstimatesForMail(t, margin.value),
                            "estimate.txt",
                        )}
                    >
                        Export mail
                    </button>
                </div>
            </div>
            <DeployInfo className="deploy-info" />
        </div>
    );
}
