import saveAs from "file-saver";
import { getAllCardsEstimates } from "../api/estimateApi";
import { Trello } from "../types/trello";
import { formatMinutes } from "./timeParsing";

function roundTo2Decimals(number: number) {
    return Math.round(number * 100)/100;
}

type Row = {
    name: string;
    minutes: number;
}

export async function getExportableRows(t: Trello.PowerUp.IFrame, marginPercentage: number) {
    const estimates = await getAllCardsEstimates(t, "id", "name");
    const filtered = estimates.filter(e => e.estimateMinutes > 0);

    const rows: Row[] = [];

    for (const estimate of filtered) {
        rows.push({
            name: estimate.card.name,
            minutes: estimate.estimateMinutes,
        });
    }

    let totalEstimateMinutes = 0;

    for (const estimate of filtered)
        totalEstimateMinutes += estimate.estimateMinutes;

    if (marginPercentage > 0) {
        const margin = totalEstimateMinutes * marginPercentage;
        rows.push({
            name: "margin",
            minutes: margin,
        });

        totalEstimateMinutes += margin;
    }

    rows.push({
        name: "total",
        minutes: totalEstimateMinutes,
    });

    return rows;
}

export async function exportEstimatesToTsv(t: Trello.PowerUp.IFrame, marginPercentage: number) {
    const rows = await getExportableRows(t, marginPercentage);

    let tsv = "Task\tHours";

    for (const row of rows)
        tsv += "\n" + row.name + "\t" + roundTo2Decimals(row.minutes/60);

    return tsv;
}

export async function exportEstimatesForMail(t: Trello.PowerUp.IFrame, marginPercentage: number) {
    const rows = await getExportableRows(t, marginPercentage);

    let mail = "";

    for (const row of rows)
        mail += row.name + "  " + formatMinutes(row.minutes) + "\n";

    return mail;
}

export async function exportToFile(contents: string, filename: string) {
    console.log(contents);
    saveAs(
        new Blob([contents], { type: "text/plain;charset=utf-8" }),
        filename,
    );
}
