import { useState } from "react";
import { getLastInputTime } from "../api/estimateApi";
import { useProvidedTrello } from "./useProvidedTrello";
import { useTrelloRender } from "./useTrelloRender";

export function useLastInputTime() {
    const t = useProvidedTrello();
    const [lastInputTime, setLastInputTime] = useState<Date | null>(null);

    useTrelloRender(t, async () => {
        const fetched = await getLastInputTime(t);
        setLastInputTime(fetched);
    });

    return lastInputTime;
}
