import React, { useEffect, useState } from "react";
import { useMarginMultiplier } from "../hooks/useMarginMultiplier";
import { useProvidedTrello } from "../hooks/useProvidedTrello";

import "./MarginInput.scss";

type Props = {
    className?: string;
}

export function MarginInput(props: Props) {
    const t = useProvidedTrello();
    const [input, setInput] = useState("");

    const margin = useMarginMultiplier(t);

    useEffect(
        () => setInput(margin.value.toString()),
        [margin.value],
    );

    return (
        <div className={`margin-input ${props.className ?? ""}`}>
            <input
                type="number"
                step={0.1}
                value={input}
                onChange={e => setInput(e.target.value)}
                placeholder='insert margin multiplier...'
            />
            <button
                onClick={() => {
                    const parsed = parseFloat(input);
                    if (parsed > 0)
                        margin.set(parsed);
                }}
            >
                Set Margin
            </button>
        </div>
    );
}
