import { useQuery, useQueryClient } from "react-query";

import { getWorkSessions } from "../api/estimateApi";
import { Trello } from "../types/trello";
import { useTrelloRenderWithOptions } from "./useTrelloRender";

const queryKey = "work_sessions";

export function useWorkSessionsQuery(t: Trello.PowerUp.IFrame) {
    return useQuery(
        queryKey,
        () => getWorkSessions(t),
        {
            staleTime: 5 * 1000,
        },
    );
}

export function useWorkSessions(t: Trello.PowerUp.IFrame) {
    const queryClient = useQueryClient();
    const query = useWorkSessionsQuery(t);

    useTrelloRenderWithOptions(t, {
        refresh: () => queryClient.setQueryData(queryKey, undefined),
        callImmediately: false,
    });

    return query;
}
