import { useCallback, useMemo, useState } from "react";
import { useQuery, useQueryClient } from "react-query";

import { getMarginMultiplier, setMaringMultiplier } from "../api/estimateApi";
import { Trello } from "../types/trello";
import { useTrelloRender } from "./useTrelloRender";

const queryKey = "margin";

export function useMarginMulitplierQuery(t: Trello.PowerUp.IFrame) {
    return useQuery(
        queryKey,
        () => getMarginMultiplier(t),
        { staleTime: 100 },
    );
}

export function useMarginMultiplier(t: Trello.PowerUp.IFrame) {
    const query = useMarginMulitplierQuery(t);
    const queryClient = useQueryClient();

    useTrelloRender(t, query.refetch);

    const set = useCallback(
        async (updatedMargin: number) => {
            await setMaringMultiplier(t, updatedMargin);
            queryClient.setQueryData(queryKey, updatedMargin);
        },
        [queryClient],
    );

    return useMemo(
        () => ({
            value: query.data ?? 0,
            set,
        }),
        [query.data, set],
    );
}
